<template>
  <div
    class="navbar bg-primary absolute top-0 text-primary-content"
    v-if="!loadingBuild"
  >
    <router-link to="/" class="btn btn-ghost normal-case text-xl"
      >web to app</router-link
    >
  </div>

  <div
    class="fixed z-[0] inset-0 w-screen bg-base-100 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"
  ></div>

  <div class="w-screen h-screen" v-if="!loadingBuild">
    <div class="absolute top-20 w-full flex justify-center flex-row gap-5">
      <img :src="logoSrc" class="w-16 h-16 rounded-box" alt="Logo" />

      <div class="flex flex-col">
        <h1 class="text-2xl">{{ appName }}</h1>
        <h1>{{ websiteUrl }}</h1>
        <h1>{{ packageName }}</h1>
        <button class="btn btn-success z-10" onclick="my_modal_2.showModal()">
          get final app
        </button>
        <dialog id="my_modal_2" class="modal">
          <div class="modal-box">
            <form method="dialog">
              <button
                class="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"
              >
                ✕
              </button>
            </form>
            <h3 class="font-bold text-lg">Enter your email to save your app</h3>
            <div class="pt-3 pb-3">
              <label for="email">Email</label>
              <input
                type="email"
                id="email"
                v-model="email"
                class="input input-bordered w-full"
                placeholder="youremail@example.com"
              />
            </div>
            <div class="pt-3 pb-3">
              <label for="password">password</label>
              <input
                type="password"
                id="password"
                v-model="password"
                class="input input-bordered w-full"
                placeholder="********"
              />
            </div>
            <div class="w-full flex justify-end">
              <button class="btn btn-success" @click="getFinalApp">
                get your app
              </button>
            </div>
            <span>{{ signInErrorMessage }}</span>
          </div>
        </dialog>
      </div>
    </div>
    <div class="w-full h-full flex justify-center items-center">
      <div
        v-if="os == 'android'"
        class="card card-side bg-base-100 shadow-2xl flex flex-col sm:flex-row mt-10"
      >
        <div class="card-body">
          <h2 class="card-title">
            <BootstrapIcon icon="android2" class="text-success" size="2x" />
            android demo
          </h2>
          <p>
            click the button to download the demo app apk or scan <br />
            the qr code
          </p>

          <div class="card-actions justify-start">
            <button class="btn btn-primary z-10" @click="downloadApk">
              <span
                v-if="watingForDownload"
                class="loading loading-spinner loading-lg text-white opacity-100"
              ></span>
              <span v-else> Download APK </span>
            </button>

            <button class="btn z-10 btn-outline" @click="editButton">
              edit
            </button>
          </div>
        </div>
        <div class="divider divider-horizontal p-0 m-0"></div>
        <figure class="p-5">
          <qrcode-vue
            :value="downloadPageUrl"
            size="200"
            level="H"
            class="z-10"
          />
        </figure>
      </div>
      <div
        v-else-if="os == 'ios'"
        class="card card-side bg-base-100 shadow-2xl flex flex-col sm:flex-row mt-10"
      >
        <div class="card-body">
          <h2 class="card-title">
            <BootstrapIcon icon="apple" class="text-gray-500" size="2x" />
            ios demo
          </h2>
          <p>
            to preview your ios app: <br />
            1. download the .zip file <br />
            2. go to
            <a
              href="https://appetize.io/upload"
              class="link link-primary"
              target="_blank"
              >appetize.io</a
            >
            <br />
            3. upload your .zip file
          </p>

          <div class="card-actions justify-start">
            <button class="btn btn-primary z-10" @click="downloadIosApp">
              Download ios zip file
            </button>

            <button class="btn z-10 btn-outline" @click="editButton">
              edit
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div
    class="w-screen h-screen flex justify-center items-center"
    v-if="loadingBuild"
  >
    <span
      class="loading loading-spinner loading-lg text-primary opacity-100"
    ></span>
  </div>

  <div
    class="w-screen h-screen absolute top-0 flex justify-center items-center z-30 bg-black bg-opacity-40"
    v-if="loadingData"
  >
    <span
      class="loading loading-spinner loading-lg text-primary opacity-100"
    ></span>
  </div>
</template>

<script>
import QrcodeVue from "qrcode.vue";
import axios from "axios";
import { supabase } from "@/supabase";
export default {
  data() {
    return {
      token: this.$route.params.token, // Replace with your actual token
      os: this.$route.params.os,
      downloadPageUrl: `http://34.125.138.233/downloadPageMobile/${this.$route.params.token}/${this.$route.params.os}`,
      appName: "",
      websiteUrl: "",
      logoSrc: "",
      packageName: "",
      email: "none",
      loadingData: true,
      loadingBuild: false,
      watingForDownload: false,
      password: "",
      signInErrorMessage: "",
      loadingAuth: false,
    };
  },

  async created() {
    const token = this.$route.params.token;
    const os = this.$route.params.os;

    if (os == "android") {
      try {
        const response = await axios.get(
          `http://34.125.138.233/get-data-android/${token}`
        );
        this.appName = response.data.name;
        this.websiteUrl = response.data.url;
        this.packageName = response.data.packageName;
        this.logoSrc = "data:image/png;base64," + response.data.logo;
        this.loadingData = false;
      } catch (error) {
        console.error("Error fetching data:", error);
        this.loadingData = false;
        if (error.response.status === 404) {
          this.$router.push({ name: "notFound" });
        }
      }
    } else if (os == "ios") {
      try {
        const response = await axios.get(
          `http://34.125.138.233/get-data-ios/${token}`
        );
        this.appName = response.data.name;
        this.websiteUrl = response.data.url;
        this.packageName = response.data.packageName;
        this.logoSrc = "data:image/png;base64," + response.data.logo;
        this.loadingData = false;
      } catch (error) {
        console.error("Error fetching data:", error);
        this.loadingData = false;
        if (error.response.status === 404) {
          this.$router.push({ name: "notFound" });
        }
      }
    }
  },

  components: {
    QrcodeVue,
  },

  methods: {
    async downloadIosApp() {
      try {
        const response = await axios.get(
          `http://34.125.138.233/download-demo-zip-ios?token=${this.token}.0`,
          {
            responseType: "blob", // Set the response type to 'blob'
          }
        );

        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "iosApp.zip"); // Set the desired file name
        document.body.appendChild(link);
        link.click();
      } catch (error) {
        console.error("Error downloading iOS app:", error);
        // Handle error as needed
      }
    },

    async downloadApk() {
      this.watingForDownload = true;
      try {
        const response = await axios.get(
          `http://34.125.138.233/download-demo-apk-android?token=${this.token}.0`,
          {
            responseType: "blob", // Set the response type to 'blob'
          }
        );
        this.watingForDownload = false;
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "apk-debu.apk"); // Set the desired file name
        document.body.appendChild(link);
        link.click();
      } catch (error) {
        this.watingForDownload = false;
        console.error("Error downloading apk", error);
        // Handle error as needed
      }
    },

    async getFinalApp() {
      this.loadingAuth = false;
      if (!this.loadingData) {
        try {
          const { data, error } = await supabase.auth.signUp({
            email: this.email,
            password: this.password,
          });

          if (error) {
            if (error.message.includes("already registered")) {
              // If the error indicates that the user already exists, try logging in
              const { data, error } = await supabase.auth.signInWithPassword({
                email: this.email,
                password: this.password,
              });

              if (error) {
                this.loadingAuth = false;
                this.signInErrorMessage = error.message;
                return;
              } else {
                console.log(data.session.user.id);
                this.loadingAuth = false;
                this.loadingBuild = true;
                try {
                  const { data: { user } } = await supabase.auth.getUser()
                  
                  const formData = new FormData();
                  formData.append("token", this.token);
                  formData.append("email", user.email);

                  const response = await axios.post(
                    "http://34.125.138.233/build-final-android",
                    formData,
                    {
                      headers: {
                        "Content-Type": "multipart/form-data",
                      },
                    }
                  );
                  console.log("Response:", response.data);
                  this.loadingBuild = false;

                  this.$router.push({
                    name: "profilePage",
                    params: {
                      email: this.email,
                    },
                  });
                } catch (error) {
                  if (error.response.status === 404) {
                    this.loadingBuild = false;
                    this.$router.push({ name: "notFound" });
                  }
                }
              }
            } else {
              this.loadingAuth = false;
              this.signInErrorMessage = error.message;
              return;
            }
          } else {
            this.loadingAuth = false;
            this.loadingBuild = true;
            console.log(data.session.user.id);
            try {
              const formData = new FormData();
              formData.append("token", this.token);
              formData.append("email", this.email);

              const response = await axios.post(
                "http://34.125.138.233/build-final-android",
                formData,
                {
                  headers: {
                    "Content-Type": "multipart/form-data",
                  },
                }
              );
              console.log("Response:", response.data);
              this.loadingBuild = false;

              this.$router.push({
                name: "profilePage",
                params: {
                  email: this.email,
                },
              });
            } catch (error) {
              if (error.response.status === 404) {
                this.loadingBuild = false;
                this.$router.push({ name: "notFound" });
              }
            }
          }
        } catch (error) {
          this.loadingAuth = false;
          console.error("Error signing up or logging in:", error.message);
          this.signInErrorMessage = error.message;
          return;
        }
      }
    },

    editButton() {
      if (!this.loadingData) {
        this.$router.push({
          name: "edit",
          params: {
            token: this.token,
            status: "demo",
            email: this.email,
          },
        });
      }
    },
  },
};
</script>

<style>
* {
  z-index: 10;
}
</style>
